import { ContentSource } from "../content.types"

const NUM_MOCK_CONTENT_SOURCES = 10

const mockContentSources: Array<ContentSource> = Array.from(
  { length: NUM_MOCK_CONTENT_SOURCES },
  (_, idx) => ({
    id: `source-${idx}`,
    name: `Content Source ${idx}`,
    packageIds: ["all", `package-${idx}`],
    providerId: `provider-${idx}`,
    entitled: idx % 3 === 0,
  }),
)

export { mockContentSources }
